@import url(https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100..900;1,100..900&family=Yeseva+One&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  transition: all 0.3s ease;
}

a, h1, h2, h3, h4, h5, h6, p {
  overflow-wrap: break-word;
}

a {
  text-decoration: none;
  color: inherit;
}

/* ROOT COLORS */
:root {
  --primary-green: #005046;
  --secondary-green: #056F62;
  --primary-red: #bb2f31e3;
  --primary-gold: #ffc107;
  --secondary-red: #F9C4D7;
  --gray: #ddd;
  --white: #FFF;
  --black: #000;
  --ash: #F0F4F1;
}

.caption-underline {
  height: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-self: center;
  align-items: center;
}
.caption-underline .green {
  width: 35%;
  height: 100%;
  background-color: var(--primary-green);
}
.caption-underline .white {
  width: 30%;
  height: 100%;
  background-color: var(--white);
}
.caption-underline .red {
  width: 35%;
  height: 100%;
  background-color: var(--primary-red);
}

.error {
  animation: shake 0.3s;
  /* color: var(--primary-red);  */
  border: solid 2px var(--primary-red) !important;
}
@keyframes shake {
  0% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  50% { transform: translateX(5px); }
  75% { transform: translateX(-5px); }
  100% { transform: translateX(0); }
}

/* MOBILE STYLING (SET TO DISPLAY NONE) */
.hamburgerMenu {
  display: none;
}
.side-bar-mobile {
  display: none;
}
.mobile-header {
  display: none;
}

/* HEADER */
header {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  grid-gap: 0;
  gap: 0;
}
header .first-header {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  grid-gap: 30px;
  gap: 30px;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 20px 50px;

}
header .first-header .f-header-left {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}
header .first-header .f-header-left img {
  width: 100px;
  height: 100px;
  object-fit: cover;
}
header .first-header .f-header-left h4 {
  color: var(--primary-green);
  font-family: "Work Sans";
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
}
header .first-header .f-header-right {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
}
header .first-header .f-header-right .header-icons {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  grid-gap: 10px;
  gap: 10px;
}
header .first-header .f-header-right .header-icons h5 {
  color: var(--primary-green);
  font-family: "Work Sans";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
header .first-header .f-header-right .header-icons p {
  color: var(--primary-red);
  font-family: "Work Sans";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
/* NAVLINKS */
header .second-header {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 20px 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  background-color: var(--primary-green);
}
header .second-header nav {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
}
header nav .navlinks {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  grid-gap: 50px;
  gap: 50px;
  list-style-type: none;
}
header nav .navlinks .navLink, 
header nav .dropdown-toggle {
  color: var(--white);
  font-family: "Work Sans";
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  background: none;
  border: none;
  cursor: pointer;
}
header nav .navlinks .active {
  color: var(--primary-red);
  font-weight: 600;
}
/* NAV DROPDOWN */
header nav .navbar-dropdown {
  position: relative;
  display: inline-block;
}
header nav .dropdown-menu {
  display: none;
  position: absolute;
  z-index: 1;
  background-color: var(--white);
  min-width: 280px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 10px;
  border-radius: 5px;
}
header nav .dropdown-menu li {
  list-style-type: none;
}
header nav .dropdown-item {
  display: block;
  padding: 10px;
  text-decoration: none;
  color: var(--black);
  font-family: "Work Sans";
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
header nav .dropdown-item:hover {
  background-color: var(--gray);
  color: var(--primary-red);
  font-weight: 500;
}
header nav .navbar-dropdown:hover .dropdown-menu {
  display: block;
}
/* SEARCHBOX */
header .nav-search {
  display: flex;
  grid-gap: 80px;
  gap: 80px;
  flex-wrap: nowrap;
  justify-content: flex-end;
  width: 400px;
}
header .search-container {
  position: relative;
  box-sizing: border-box;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
header .search-mainbox {
  box-sizing: border-box;
  position: relative;
  width: 230px;
  height: 50px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  border-radius: 160px;
  background-color: var(--white);
  transition: all 0.3s ease;
}
header .search-checkbox:focus {
  border: none;
  outline: none;
}
header .search-checkbox:checked {
  right: 10px;
}
header .search-checkbox:checked ~ .search-mainbox {
  width: 50px;
}
header .search-checkbox:checked ~ .search-mainbox .search_input {
  width: 0;
  height: 0px;
}
header .search-checkbox:checked ~ .search-mainbox .search-iconContainer {
  padding-right: 8px;
}
header .search-checkbox {
  box-sizing: border-box;
  width: 30px;
  height: 30px;
  position: absolute;
  right: 17px;
  top: 12px;
  z-index: 9;
  cursor: pointer;
  -webkit-appearance: none;
          appearance: none;
}
header .search_input {
  box-sizing: border-box;
  height: 100%;
  width: 170px;
  background-color: transparent;
  border: none;
  outline: none;
  padding-bottom: 4px;
  padding-left: 10px;
  font-size: 18px;
  font-weight: 500;
  color: var(--primary-green);
  transition: all 0.3s ease;
  font-family: 'Work Sans';
}
header .search_input::placeholder {
  color: var(--secondary-green);
  font-family: 'Work Sans';
  font-weight: 400;
  font-size: 16px;
}
header .iconContainer {
  box-sizing: border-box;
  padding-top: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  transition: all 0.3s ease;
}
header .search_icon {
  box-sizing: border-box;
  fill: var(--primary-green);
  font-size: 1.3em;
}
/* PROFILE MENU */
header .profile-button {
  background-color: transparent;
  background: none;
  border: none;
  cursor: pointer;
}
header .profile-menu {
  position: absolute;
  top: 220px; 
  right: 30px; 
  background-color: var(--white); 
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
  z-index: 10;
}
header .profile-menu input,
header .profile-menu button {
  display: block;
  margin-bottom: 10px;
  width: 200px; 
  padding: 10px;
  border: none;
  border-radius: 5px;
  box-sizing: border-box;
  font-family: "Work Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
header .profile-menu input:placeholder-shown {
  font-size: 14px;
  font-family: 'Work Sans';
  color: var(--secondary-green);
}
header .profile-menu .signin-button {
  background-color: var(--primary-green);
  color: var(--white);
  cursor: pointer;
}
header .profile-menu .create-account-button {
  background-color: transparent;
  color: var(--primary-red);
  text-decoration: underline;
  cursor: pointer;
}
header .profile-menu .signin-button:hover {
  background-color: var(--secondary-green);
}
header .password-input {
  position: relative;
}
header .password-input input {
  padding-right: 40px;
}
header .toggle-password-icon {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  cursor: pointer;
}
/* FOOTER  */
footer {
